import { graphql, StaticQuery } from "gatsby";
import * as React from "react";
import Helmet from "react-helmet";
import Avatar from "../header/avatar";
import Navigation from "../header/navigation";
import { CentredRow, Container } from "./grid";
import { injectGlobal } from "styled-components";
// import GlobalStyle from "./global_style";

injectGlobal`
  @import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400,700');

  body {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.2em;
    color: hsla(0,0%,0%,0.7);
    font-weight: 400;
  }

  h1, h2, h3, h4, h5 {
    color: hsla(0,0%,0%,0.85);
    font-weight: 200;
  }

  p {
    line-height: 1.56em;
  }

  a, a:visited {
    text-decoration: none;
    color: #419EDA;

    &:hover {
      text-decoration: underline;
      color: #2a6496;
    }
  }
`;

const PageLayout = (props: { children: React.ReactNode }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
            avatar
            navigation {
              path
              name
            }
          }
        }
      }
    `}
    // tslint:disable-next-line:jsx-no-lambda
    render={data => (
      <React.Fragment>
        <Helmet title={data.site.siteMetadata.title} />
        <Container>
          <CentredRow>
            <Avatar
              imageUrl={data.site.siteMetadata.avatar}
              name={data.site.siteMetadata.title}
            />
          </CentredRow>

          <Navigation links={data.site.siteMetadata.navigation} />

          {props.children}
        </Container>
      </React.Fragment>
    )}
  />
);

export default PageLayout;
